<template>
  <div id="rank" :style="{
  'background': `url('static/rank/banner.jpg') no-repeat 0 0 / cover fixed`}">
    <div class="inner">
      <div class="search">
        <input ref="search" type="text" placeholder="请输入关键词" @keyup.enter="search">
        <button @click="search">搜索</button>
        <span v-if="isShow">该城市不存在！</span>
      </div>
      <div class="title">
        <h3>空气质量排行榜(根据AQI排序)</h3>
        <!--<p>数据更新时间：<span></span></p>-->
      </div>
      <div class="list" ref="list">
        <table border="1">
          <thead>
          <tr>
            <th>排名</th>
            <th>城市</th>
            <th @click="sortList('num')">AQI</th>
            <th>空气质量</th>
            <th @click="sortList('pm25')">PM<sub>2.5</sub></th>
            <th @click="sortList('pm10')">PM<sub>10</sub></th>
            <th @click="sortList('co')">CO</th>
            <th @click="sortList('no2')">NO<sub>2</sub></th>
            <th @click="sortList('o3')">O<sub>3</sub></th>
            <th @click="sortList('so2')">SO<sub>2</sub></th>
          </tr>
          </thead>
          <tbody>
          <tr v-if="!PC" v-for="(item,index) in aqiList" class="phoneList" ref="top">
            <td>
              <span class="listTitle">排名</span>
              <span class="listCon">{{index+1}}</span></td>
            <td>
              <span class="listTitle">城市</span>
              <span class="listCon">
                <a href="javascript:;" @click="goToSmog(item.cityname)" v-html="item.cityname" ref="city"></a>
              </span>
            </td>
            <td>
              <span class="listTitle">AQI</span>
              <span class="listCon" v-html="item.num"></span>
            </td>
            <td>
              <span class="listTitle">空气质量</span>
              <span class="listCon" v-html="item.status"></span>
            </td>
            <td>
              <span class="listTitle">PM<sub>2.5</sub></span>
              <span class="listCon" v-html="item.pm25"></span>
            </td>
            <td>
              <span class="listTitle">PM<sub>10</sub></span>
              <span class="listCon" v-html="item.pm10"></span>
            </td>
            <td>
              <span class="listTitle">CO</span>
              <span class="listCon" v-html="item.co"></span>
            </td>
            <td>
              <span class="listTitle">NO<sub>2</sub></span>
              <span class="listCon" v-html="item.no2"></span>
            </td>
            <td>
              <span class="listTitle">O<sub>3</sub></span>
              <span class="listCon" v-html="item.o3"></span>
            </td>
            <td>
              <span class="listTitle">SO<sub>2</sub></span>
              <span class="listCon" v-html="item.so2"></span>
            </td>
          </tr>
          <tr v-if="PC" v-for="(item,index) in aqiList" class="webList" ref="name">
            <td>{{index+1}}</td>
            <td>
              <a href="javascript:;" @click="goToSmog(item.cityname)" v-html="item.cityname" ref="webCity"></a>
            </td>
            <td v-html="item.num==='0'?'暂无':item.num"></td>
            <td v-html="item.status"></td>
            <td v-html="item.pm25"></td>
            <td v-html="item.pm10"></td>
            <td v-html="item.co"></td>
            <td v-html="item.no2"></td>
            <td v-html="item.o3"></td>
            <td v-html="item.so2"></td>
          </tr>
          </tbody>
        </table>
      </div>
      <div class="none" v-if="isBlock">
        <span>暂无数据！稍后再试</span>
      </div>
      <foot-box></foot-box>
    </div>
    <go-top></go-top>
    <loading v-if="!aqiList.length && !error"></loading>
    <wrong v-if="error"></wrong>
  </div>
</template>

<script type="text/ecmascript-6">
  import { ERROR } from 'api/config'
  import { getRankList } from 'api/getRankList'
  import { compare } from 'common/js/compatible'
  import { mapMutations } from 'vuex'

  import FootBox from 'components/footer/footer'
  import goTop from 'base/goTop/goTop'
  import Loading from 'base/loading/loading'
  import Wrong from 'base/error/error'

  export default {
    data () {
      return {
        error: false,
        aqiList: [],
        isShow: false,
        isBlock: false
      }
    },
    methods: {
      goToSmog (name) {
        this.setSmogCity(name)
        this.$router.push({
          path: '/smog'
        })
      },
      _getRankList () {
        getRankList().then((res) => {
          if (res.data.error === ERROR) {
            this.aqiList = res.data.data
            if (this.aqiList.length === 0) {
              this.isBlock = true
            }
          } else {
            this.error = true
          }
        }).catch((res) => {
          this.error = true
        })
      },
      search () {
        if (document.body.clientWidth >= 750) {
          let val = this.$refs.search.value
          for (let i = 0; i < this.$refs.webCity.length; i++) {
            if (val === this.$refs.webCity[i].innerHTML) {
              this.isShow = false
              window.scrollTo(0, this.$refs.name[i].offsetTop + this.$refs.list.offsetTop)
              return
            } else {
              this.isShow = true
            }
          }
        }
        if (document.body.clientWidth <= 750) {
          let value = this.$refs.search.value
          for (let j = 0; j < this.$refs.city.length; j++) {
            if (value === this.$refs.city[j].innerHTML) {
              this.isShow = false
              window.scrollTo(0, this.$refs.top[j].offsetTop + this.$refs.list.offsetTop)
              return
            } else {
              this.isShow = true
            }
          }
        }
      },
      sortList (text) {
        let obj = this.aqiList
        obj = obj.sort(compare(text))
        this.aqiList = obj
      },
      ...mapMutations({
        setSmogCity: 'SET_SMOG_CITY'
      })
    },
    mounted () {
      this._getRankList()
      document.body.clientWidth >= 750 ? this.PC = true : this.PC = false
      window.addEventListener('resize', () => {
        document.body.clientWidth >= 750 ? this.PC = true : this.PC = false
      })
    },
    components: {
      FootBox,
      goTop,
      Loading,
      Wrong
    }
  }
</script>

<style lang="less" scoped rel="stylesheet/less" type="text/less">
  @media screen and (max-width: 750px) {
    #rank {
      .inner {
        .list {
          table {
            border: 0;
          }

          table thead {
            display: none;
          }

          table tbody .webList {
            display: none;
          }

          table tbody .phoneList {
            display: block !important;
            td {
              a {
                color: #08c;
                &:hover {
                  color: #005580;
                  text-decoration: underline;
                }
              }
              .listTitle {
                display: inline-block;
                float: left;
              }
              .listCon {
                display: inline-block;
                text-align: right;
              }
            }
          }
          table tr {
            display: block;
            &:nth-child(2n) {
              background-color: rgba(255, 255, 255, 0.3);
            }
          }

          table td {
            display: block;
            text-align: right;
            padding: 5px;
            box-sizing: border-box;
          }
        }
      }
    }
  }

  @media screen and (max-width: 414px) {
    #rank {
      .inner {
        width: 95% !important;
      }
    }
  }

  #rank {
    width: 100%;
    padding-top: 100px;
    padding-bottom: 20px;
    position: relative;
    min-height: 100%;
    .inner {
      margin-top: 10px;
      .none {
        width: 100%;
        margin: auto 0;
        text-align: center;
        span {
          color: #eee;
          font-size: 28px;
        }
      }
      .search {
        width: 100%;
        color: #fff;
        input {
          width: 50%;
          height: 40px;
          border-radius: 20px;
          padding: 0 10px;
          box-sizing: border-box;
          background-color: rgba(0, 0, 0, 0.5);
          border: 0;
          color: #eee;
          outline: none;
          margin-right: 5%;
        }
        span {
          color: red;
          font-size: 12px;
          display: block;
          padding: 5px 10px;
          box-sizing: border-box;
        }
        button {
          width: 20%;
          height: 40px;
          border-radius: 10px;
          background-color: rgba(0, 0, 0, 0.5);
          border: 0;
          color: #eee;
          cursor: pointer;
          outline: none;
        }
      }
      .canvas {
        width: 100%;
        margin-bottom: 15px;
        #chart {
          float: left;
          width: 49.5%;
          margin-right: 1%;
          height: 400px;
          border-radius: 20px;
        }
        #chart2 {
          float: left;
          width: 49.5%;
          height: 400px;
          border-radius: 20px;
        }
      }
      .title {
        h3 {
          font-size: 24.5px;
          line-height: 40px;
          margin: 10px 0;
          font-weight: bold;
        }
        p {
          margin: 0 0 10px;
          font-size: 14px;
          line-height: 20px;
          color: #333;
        }
      }
      .list {
        width: 100%;
        margin-bottom: 20px;
        clear: both;
        background-color: rgba(0, 0, 0, .5);
        table {
          font-size: 14px;
          width: 100%;
          clear: both;
          border: 0.1px solid rgba(255, 255, 255, .5);
          border-collapse: separate;
          border-left: 0;
          border-radius: 4px;
          border-spacing: 0;
          color: #eee;
          thead {
            tr {
              th {
                cursor: pointer;
                border-top: 0;
                vertical-align: bottom;
                text-align: center;
                border-left: 0.1px solid rgba(255, 255, 255, .5);
                padding: 4px 5px;
                font-weight: bold;
                line-height: 20px;
              }
            }
          }
          tbody {
            .phoneList {
              display: none;
            }
            .webList {
              td {
                border-left: 0.1px solid rgba(255, 255, 255, .5);
                padding: 4px 5px;
                line-height: 40px;
                text-align: left;
                vertical-align: top;
                border-top: 0.1px solid rgba(255, 255, 255, .5);
                a {
                  color: #08c;
                  &:hover {
                    color: #005580;
                    text-decoration: underline;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
</style>



