<template>
  <transition name="slide">
    <div id="goTop" @click="goTop" v-if="isShow">
      <img src="./go-to-top.png" alt="top">
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import {getScrollTop} from 'common/js/compatible'

  export default {
    data() {
      return {
        scroll: 0,
        isShow: false
      }
    },
    methods: {
      goTop() {
        this.scroll = getScrollTop()
        this.n = this.scroll
        this.timer = setInterval(() => {
          this.scroll -= this.n * 0.03 / 0.5
          window.scrollTo(0, this.scroll)
          if (this.scroll <= 0) {
            clearInterval(this.timer)
            window.scrollTo(0, 0)
          }
        }, 30)
      }
    },
    mounted() {
      window.addEventListener('scroll', () => {
        getScrollTop() >= 300 ? this.isShow = true : this.isShow = false
      })
    }
  }
</script>

<style lang="less" scoped rel="stylesheet/less" type="text/less">
  #goTop {
    position: fixed;
    bottom: 5%;
    right: 20px;
    cursor: pointer;
    width: 50px;
    height: 50px;
    padding-top: 9px;
    padding-left: 9px;
    box-sizing: border-box;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 50%;
    z-index: 9999999;
    transition: all 0.3s;
    &.slide-enter {
      transform: scale(0) rotateY(-180deg);
      opacity: 0;
    }
    &.slide-enter-active {
      transition: all 0.3s;
    }
    &.slide-leave {
      transition: all 0.3s;
    }
    &.slide-leave-active {
      transform: scale(0) rotateY(180deg);
      opacity: 0;
    }
    img {
      z-index: 99;
    }
    span {
      position: absolute;
      top: 15px;
      right: 18px;
      color: #FFF;
      display: inline-block;

    }
  }
</style>



